import React, { Component } from 'react'

import { Fragment } from 'react'
import { MdHistory, MdInfo } from 'react-icons/md'
import { Redirect } from 'react-router-dom'
import Modal, { Themes, ModalSize } from '../../components/Modal/Modal'
import ChooseExamCenter, { LocationTableInterface } from '../Application/ExamCenter/ChooseExamCenter'
import { connect } from "react-redux";
import {
  Auth,
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  examDataInterface,
  PrepareExamsStoreInterface,
  loadExamsList,
  LocationsDataStoreInterface,
  loadAllLocationsData,
  setSelectedLocationData,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  candidateExamStoreInterface,
  setSelectedCandidateExam,
  courseStoreInterface,
  loadCourses,
  setSelectedCourse,
  InitializeExamDetails,
  InitialExamDetails,
  setSelectedPeriod,
} from "../../actions";
import { StoreState } from "../../reducers";
import Loading from '../../components/Loading/Loading'
import { ExamInterface, examStatus } from '../../interfaces/exam'
import { course, PositionAPI } from '../../interfaces/index'
import { applicationExamAPI, applicationHistory, ApplicationPeriodGet, ApplicationStates, GetApplicationsAPI } from '../../interfaces/application'
import { sentExamInterface } from '../Application/ExamCenter/ExamCenter'
import Alert, { AlertInterface } from '../../components/Alert/Alert'
import { RiErrorWarningLine } from 'react-icons/ri'
import { BsFileEarmarkText } from 'react-icons/bs'
import { IoMdTrash } from 'react-icons/io'
import { DATE } from '../../utils/functions'
import { ExamItemResponse } from '../../interfaces/prepare_exam'
import ApplicationHistory from './ApplicationHistory/ApplicationHistory'
import ExamDetails from './ExamDetails/ExamDetails'

interface PostExamsProps {
  doExam: examDataInterface;
  auth: Auth;
  loadExam: (user_id: string, exam: ExamInterface) => void;
  selectedPosition: PositionAPI | null;
  selectedExams: applicationExamAPI[] | null;
  selected_application: string | null;
  updateLocation: (application_id: string, exams: sentExamInterface[]) => void;
  changeLocationError: string | null,
  loading: boolean,
  incrementIncomplete: () => void;
  applicationHistory: applicationHistory[] | undefined;
  deleteApplication: (id: string) => void;
  selectedApplicationDetails: GetApplicationsAPI | null;
  locationsData: LocationsDataStoreInterface;
  loadAllLocationsData: (callback: (loading: boolean) => void) => void;
  setSelectedLocationData: (locationData: LocationTableInterface) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  candidateExam: candidateExamStoreInterface;
  setSelectedCandidateExam: (exam_id: string) => void;
  course: courseStoreInterface;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
  InitializeExamDetails: (periodId: string, courseId: string, callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
}
interface PostExamsState {
  appointmentDetailsModal: {
    status: boolean;
    item: InitialExamDetails | null;
  };
  redirect: boolean;
  displayResults: boolean;
  changeLocation: {
    status: boolean;
    location: ExamInterface | null;
  };
  centersModal: boolean;
  selectedCourse: course | null,
  selectedLocation: applicationExamAPI | null,
  applicationHistoryState: boolean;
  loading: boolean;
}

class _PostExams extends Component<PostExamsProps, PostExamsState> {
  constructor(props: PostExamsProps) {
		super(props);

		this.state = {
      appointmentDetailsModal: {
        status: false,
        item: null,
      },
      redirect: false,
      displayResults: false,
      changeLocation: {
        status: false,
        location: null,
      },
      centersModal: false,
      selectedCourse: null,
      selectedLocation: null,
      applicationHistoryState: false,
      loading: false,
    };
	}

  renderExam = (user_id: string, exam: ExamInterface) => {
    exam.status = examStatus.Editing;
    this.props.loadExam(user_id, exam);
    console.log("Selected exam: ", user_id, exam);
    this.setState({ redirect: true });
  }

  closeModal = () => {
    this.setState({ centersModal: false });
  }

  updateLocationFunction = (exams: sentExamInterface[]) => {
    if (this.props.selected_application !== null) {
      this.props.updateLocation(this.props.selected_application, exams);
    }
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status});
  }

  numberOfIncomplete = (
    exams: sentExamInterface[], courses: {
    course_name: string;
    course_code: string;
    description: string;
    school_levels: string[];
    course_id: string;
    }[]) => {
    let count: number = 0;
      for (let i in courses) {
        if (exams.filter(item => item.course_id === courses[i].course_id).length === 0) {
          count = count + 1;
        }
      }
    return count;

  }

  findLocation = (
    course_id: string, 
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string,
  ) => {
    return this.updateLocationFunction([{
      course_id: course_id, 
      proctoring_type: "Center",
      center_id: location.center_id
    }])
  }

  findExam = (courseId: string) => {
    let exam: InitialExamDetails | null | undefined = this.props.candidateExam.selected_exam_details.find(itm => itm.course === courseId);
    if (exam !== undefined && exam !== null) {
      return exam;
    } else {
      return null;
    }
  }

  findCourse = (course_id: string): applicationExamAPI | null => {
    if (this.props.selectedExams !== null) {
      let x: applicationExamAPI | null | undefined = this.props.selectedExams.find(itm => this.state.appointmentDetailsModal && itm.course_id === course_id)
      if (x !== undefined && x !== null) {
        return x;
      }
    }
    return null
  }

  componentDidMount = () => {
    // if (this.props.periods.applicationPeriods === null) {
      this.props.loadApplicationPeriods(this.setLoading);
    // }
    // if (this.props.selectedApplicationDetails && this.props.selectedApplicationDetails.application_period) {
    //   if (this.props.periods.selected_period !== null && this.props.periods.selected_period.application_session_id !== this.props.selectedApplicationDetails.application_period) {
    //     if (this.props.periods.applicationPeriods !== null) { 
    //       let find = this.props.periods.applicationPeriods.find(itm => this.props.selectedApplicationDetails && itm.application_session_id === this.props.selectedApplicationDetails.application_period);
    //       if (find !== undefined) {
    //         this.props.setSelectedPeriod(find);
    //       }
    //     }
    //   }
    // }
    if (this.props.periods.selected_period !== null && this.props.selectedPosition !== null) {
      for(let course of this.props.selectedPosition.courses) {
        let check = this.props.candidateExam.selected_exam_details.find(itm => itm.course === course.course_id);
        if (check === undefined) {
          this.props.InitializeExamDetails(
            this.props.periods.selected_period.application_session_id,
            course.course_id,
            this.setLoading
          )
        }
      }
    }
    if (this.props.doExam.exam_answers) {
      if (this.props.doExam.exam_answers.status === examStatus.Editing) {
        this.setState({ redirect: true });
      }
    }
    if (this.props.course.courses === null || this.props.course.courses.length === 0) {
      this.props.loadCourses(this.setLoading);
    }
  }

  render() {
    if (this.props.candidateExam.selected_exam_id !== null) {
      return <Redirect to="/do-exam" />
    }
    if (this.state.redirect === true) {
      return <Redirect to="/do-exam" />
    }
    if (this.props.loading === true) { return <Loading /> }
    return (
      this.props.selectedExams === null ? "No result found!" :
      <Fragment>
        <div className="">
          {console.log("Exams: ", this.props.doExam)}
          <div className="px-2 md:py-2">
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center">
              <div onClick={() => this.setState({ applicationHistoryState: true })}className="btn bg-gray-100 text-gray-600 border border-gray-300 text-sm flex flex-row items-center gap-2 cursor-pointer hover:border-blue-500 hover:bg-blue-50 hover:text-blue-600 my-3 md:mt-0">
                <MdHistory className="text-xl" />
                <span>View application history</span>
              </div>
              <div className="grid grid-cols-3 gap-4">
                <div className="col-span-1 flex justify-start md:justify-end">
                  <div className="flex flex-row items-center">
                    {/* <MdBookmark className="text-5xl text-blue-500" /> */}
                    <div className="flex flex-col">
                      <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">Total</span>
                      <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">{this.props.selectedExams.length}</span>
                    </div>
                  </div>
                </div>
                <div className="col-span-1 flex justify-start md:justify-end">
                  <div className="flex flex-row items-center">
                    {/* <MdAssignment className="text-5xl text-blue-500" /> */}
                    <div className="flex flex-col">
                      <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">Pending</span>
                      <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">{
                      this.props.selectedPosition !== null &&
                      this.numberOfIncomplete(this.props.selectedExams, this.props.selectedPosition.courses)
                      }</span>
                    </div>
                  </div>
                </div>
                <div className="col-span-1 flex justify-start md:justify-end">
                  <div className="flex flex-row items-center">
                    {/* <MdAssignmentTurnedIn className="text-5xl text-blue-500" /> */}
                    <div className="flex flex-col">
                      <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">Total marks</span>
                      <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">0%</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="font-bold flex flex-col md:flex-row items-start md:items-center justify-start md:justify-between gap-2 mt-8">
              {this.props.selectedApplicationDetails && this.props.selectedApplicationDetails.application_status !== ApplicationStates.Rejected ?
              <div className="text-xl flex flex-row items-center gap-2">
                <BsFileEarmarkText className="text-2xl" />
                <span>Exams list</span>
              </div> : <div></div>}
              <div onClick={() => this.props.selected_application && this.props.deleteApplication(this.props.selected_application)} className="btn flex flex-row gap-2 items-center text-red-700 bg-red-50 cursor-pointer hover:bg-red-100 hover:text-red-900">
                <IoMdTrash className="text-xl " />
                <span className="text-sm">Remove application</span>
              </div>
            </div>
          </div>
          {this.props.selectedApplicationDetails && this.props.selectedApplicationDetails.application_status !== ApplicationStates.Rejected &&
          <div className="px-2 pb-2 mt-3">
            <div>
              {this.props.changeLocationError !== null && 
              <Alert 
                title="Error"
                description={this.props.changeLocationError}
                theme={AlertInterface.danger}
              />}
            </div>
            {this.state.loading === true ? 
            <div className="rounded border px-4 py-4 flex flex-col gap-3">
              <span className="text-gray-500 text-xl ml-4">Loading...</span>
              <Loading />
            </div> :
            <div className="grid md:grid-cols-1 lg:grid-cols-1 gap-3">
              
              {this.props.selectedPosition === null ? <Loading /> : 
              this.props.selectedPosition.courses.length === 0 ? "No Exams found!" :
              this.props.selectedPosition.courses.map((item, i) => (
                <>
                {this.props.loading === true && this.state.selectedCourse?.course_id === item.course_id ? <Loading /> : 
                <div key={i + 1} className={`border px-2 py-2 rounded ${this.state.selectedCourse?.course_id === item.course_id ? "border-blue-500 bg-blue-50" : this.props.selectedExams === null ? "" : this.props.selectedExams.find(itm => itm.course_id === item.course_id) !== undefined ? "bg-white border-blue-200 hover:bg-blue-100" : "bg-yellow-50 hover:bg-white border-yellow-600"}`}>
                  <div className="grid md:grid-cols-12 gap-2">
                    <div className="col-span-4 flex fex-row items-center gap-3">
                      <div className="flex flex-col">
                        <span className={`font-bold ${this.props.selectedExams?.find(itm => itm.course_id === item.course_id) !== undefined ? "text-blue-600" : "text-yellow-700"} text-lg truncate`}>{item.course_name}</span>
                        <div className="flex flex-row justify-start items-center gap-2">
                          <span className="text-gray-500 text-xs">Exam date: </span>
                          {this.findExam(item.course_id) !== null ? 
                          <div className="flex flex-row items-center gap-2">
                            <span className="font-bold text-blue-600 text-sm">{DATE(this.findExam(item.course_id)!.exam_date, "DD/MM/YYYY")}</span>
                            <span className={`bg-blue-100 text-blue-600 font-bold px-2 py-1 rounded-full text-sm`}>{this.findExam(item.course_id)!.start_time}</span>
                          </div> :
                          <span className={`text-gray-600 text-sm italic`}>Waiting for preparation</span>}
                        </div>
                      </div>
                    </div>
                    <div className="col-span-4 flex flex-col items-start justify-center">
                    {this.props.selectedExams === null ? "" : this.props.selectedExams.find(itm => itm.course_id === item.course_id) !== undefined ? 
                      <>
                        <div className="flex flex-row justify-start items-center gap-2">
                          <span className="text-gray-500 text-xs">Proctoring type: </span>
                          <span className="text-gray-700 font-bold text-sm">{this.props.selectedExams === null ? "" : this.props.selectedExams.find(itm => itm.course_id === item.course_id) !== undefined && this.props.selectedExams.find(itm => itm.course_id === item.course_id)?.proctoring_type}</span>
                        </div>
                        <div className="flex flex-row justify-start items-center gap-2">
                          <span className="text-gray-500 text-xs">Exam location: </span>
                          <span className={`text-gray-700 font-bold text-sm`}>{this.props.selectedExams === null ? "" : this.props.selectedExams.find(itm => itm.course_id === item.course_id) !== undefined ? this.props.selectedExams.find(itm => itm.course_id === item.course_id)?.center_name : <span className="italic text-xs">No location selected</span>}</span>
                        </div>
                      </> : 
                      <div className="flex flex-row gap-2 items-center justify-start">
                        <RiErrorWarningLine className="text-xl text-yellow-700" />
                        <span className="text-yellow-700 italic text-sm">No location selected</span>
                      </div>}
                    </div>
                    <div className="col-span-4 flex items-center justify-start md:justify-end">
                    {this.props.selectedExams === null ? "" : this.props.selectedExams.find(itm => itm.course_id === item.course_id) !== undefined ?  
                      <div className="flex flex-row justify-start md:justify-end items-center gap-2">
                        <div onClick={() => this.setState({ selectedCourse: item, centersModal: true })} className="btn text-blue-600 border border-white cursor-pointer hover:bg-blue-600 hover:text-white bg-blue-100 text-sm w-40 text-center">Change location</div>
                        <div onClick={() => {
                          this.props.course.courses && this.props.setSelectedCourse(this.props.course.courses.find(itm => itm.course_id === item.course_id)!);
                          this.setState({ appointmentDetailsModal: { status: true, item: this.findExam(item.course_id)} });
                        }} className="btn border border-blue-500 text-blue-600 cursor-pointer hover:bg-blue-600 hover:text-white text-sm text-center">Details</div>
                        {this.props.periods.selected_period !== null && this.props.periods.selected_period.is_active === true &&
                        <div onClick={() => {
                          this.props.course.courses && this.props.setSelectedCourse(this.props.course.courses.find(itm => itm.course_id === item.course_id)!);
                          this.props.setSelectedCandidateExam(this.findExam(item.course_id)!.id);
                        }} className="btn border border-blue-500 bg-blue-600 text-white cursor-pointer hover:bg-white hover:text-blue-600 text-sm w-28 text-center">Start exam</div>}
                      </div> : 
                      <div className="flex flex-row justify-start md:justify-end items-center gap-2">
                        <div onClick={() => this.setState({ selectedCourse: item, centersModal: true })} className="btn cursor-pointer bg-yellow-700 hover:bg-yellow-50 border border-yellow-700 hover:text-yellow-700 text-white text-sm w-36 text-center">Choose location</div>
                      </div>}
                    </div>
                  </div>
                </div>}
                </>
              ))
            }
            </div>}
          </div>}
        </div>
        {this.state.appointmentDetailsModal.status === true &&
        <Modal
          title={<div className="text-blue-600 font-bold text-2xl">{this.state.appointmentDetailsModal.item !== null && <>{this.findCourse(this.state.appointmentDetailsModal.item!.course)?.course_name}</>}</div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ appointmentDetailsModal: { status: false, item: null}, displayResults: false })}
          backDropClose={false}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{title: true,}} >
          {this.state.appointmentDetailsModal.item !== null ?
          <>
            {this.findExam(this.state.appointmentDetailsModal.item.course) === null ? 
            <div className="text-yellow-700 font-bold text-center text-3xl bg-yellow-50 border border-yellow-200 rounded px-4 py-4 mx-4 my-4">
            No exam found for selected course</div> :
            this.props.periods.applicationPeriods && 
            (this.props.periods.applicationPeriods.find(itm => this.state.appointmentDetailsModal.item && itm.application_session_id === this.state.appointmentDetailsModal.item.period)?.is_active === false ||
            this.props.periods.applicationPeriods.find(itm => this.state.appointmentDetailsModal.item && itm.application_session_id === this.state.appointmentDetailsModal.item.period)?.is_active === undefined) ?
            <div className="text-yellow-700 font-bold text-center text-3xl bg-yellow-50 border border-yellow-200 rounded px-4 py-4 mx-4 my-4 flex flex-col items-center">
              <div>No exam for the selected period</div>
              <div onClick={() => this.props.loadApplicationPeriods(this.setLoading)} className="btn bg-white text-gray-700 w-max text-base border border-gray-700 mt-4 cursor-pointer">Reload</div>
            </div> :
            <ExamDetails 
              locationDetails={this.props.selectedExams.find(itm => this.state.appointmentDetailsModal.item && itm.course_id === this.state.appointmentDetailsModal.item.course)}
              examDetails={this.findExam(this.state.appointmentDetailsModal.item.course)}
              locationsData={this.props.locationsData}
              loadAllLocationsData={this.props.loadAllLocationsData}
              setSelectedLocationData={this.props.setSelectedLocationData} 
              periods={this.props.periods} 
              loadApplicationPeriods={this.props.loadApplicationPeriods}
              candidateExam={this.props.candidateExam} 
              setSelectedCandidateExam={this.props.setSelectedCandidateExam} />}
          </> : 
          <div className="rounded bg-yellow-100 px-2 py-2 flex flex-col gap-4 items-center justify-center">
            <MdInfo className="text-9xl text-yellow-800" />
            <div className="text-4xl font-bold text-yellow-800">
              No Exam found!
            </div>
          </div>}
        </Modal>}
        {this.state.centersModal === true && this.state.selectedCourse !== null &&
        <Modal
          title={<div className="text-gray-800 font-bold text-2xl">Choose exam center location</div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ centersModal: false })}
          backDropClose={false}
          widthSizeClass={ModalSize.maxWidth}
          displayClose={true}
          padding={{title: true,}} >
          {this.props.selected_application &&
          this.props.selectedPosition &&
            <ChooseExamCenter
              positionCourses={this.state.selectedCourse}
              addLocation={this.findLocation}
              closeModal={this.closeModal}
            />}
        </Modal>
        }
        {this.props.applicationHistory && console.log("History: ", this.props.applicationHistory)}
        {this.state.applicationHistoryState === true &&
        <Modal
          title={<div className="text-blue-800 font-bold text-xl flex flex-row justify-center items-center gap-2"><MdHistory className="text-3xl" /><span>Application status history</span></div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ applicationHistoryState: false })}
          backDropClose={true}
          widthSizeClass={ModalSize.large}
          displayClose={true}
          padding={{title: true,}} >
            {this.props.applicationHistory === undefined ?  <div className="text-yellow-700 font-bold text-2xl text-center">No application history found!</div> :
            <ApplicationHistory applicationHistory={this.props.applicationHistory} />}
        </Modal>}
      </Fragment>
    )
  }
}


const mapStateToProps = ({
  auth,
  doExam,
  prepareExam,
  locationsData,
  periods,
  candidateExam,
  course,
}: StoreState): { auth: Auth; doExam: examDataInterface, prepareExam: PrepareExamsStoreInterface; locationsData: LocationsDataStoreInterface; periods: applicationPeriodsStoreInterface; candidateExam: candidateExamStoreInterface; course: courseStoreInterface; } => {
  return { auth: auth, doExam: doExam, prepareExam: prepareExam, locationsData: locationsData, periods: periods, candidateExam: candidateExam, course: course };
};

const PostExams = connect(mapStateToProps, {
  fetchAuth,
  FC_GetTwoFactorAuth,
  FC_ChangeTwoFactorType,
  FC_Login,
  FC_LoginCheckCode,
  loadExam,
  loadExamsList,
  loadAllLocationsData,
  setSelectedLocationData,
  loadApplicationPeriods,
  setSelectedCandidateExam,
  loadCourses,
  setSelectedCourse,
  InitializeExamDetails,
  setSelectedPeriod,
})(_PostExams);

export default PostExams;