import React, { Component, Fragment } from 'react'
import { MdAdd, } from 'react-icons/md'
import { Link, Redirect } from 'react-router-dom'
import { connect } from "react-redux";
import {
  Auth,
  initiateApplication,
  removeLocation,
  loadApplicationPeriods,
  applicationPeriodsStoreInterface,
  setSelectedPeriod,
  System,
  PushPopupAlert,
  positionsStoreInterface,
  loadAllPositions,
  FC_Access,
} from "../../actions";
import { StoreState } from "../../reducers";
import { applicationStoreInterface, initialApplicationInterface } from '../../actions/application';
import { ApplicationStepsTypes } from '../Application/ApplicationSteps/ApplicationSteps';
import { applicationExamAPI, ApplicationPeriodGet, ApplicationStates, ApplicationStatesDescription, GetApplicationsAPI, NoRoomsInterface } from '../../interfaces/application';
import Exams from './Exams';
import { PositionAPI } from '../../interfaces/index';
import axios from 'axios';
import { API } from '../../utils/api';
import { sentExamInterface } from '../Application/ExamCenter/ExamCenter';
import Loading from '../../components/Loading/Loading';
import { AiOutlineFilter } from 'react-icons/ai';
import { IoBriefcase, IoBriefcaseOutline } from 'react-icons/io5';
import Modal, { ModalSize, Themes } from '../../components/Modal/Modal';
import { SideNavigation, MainItem, } from '../../components/Navigation/SideNavigation';
import { BsCaretRightFill } from 'react-icons/bs';
import LoadingV2, { LoadingType } from '../../components/Loading/LoadingV2';
import { PopupAlertData } from '../../components/Alert/Popup';
import { PopupAlertTheme } from '../../interfaces/popupAlert';
import { RiFileForbidLine } from 'react-icons/ri';
import { IoMdList } from 'react-icons/io';
import NoResult, { NoResultThemes } from '../../components/NoResult/NoResult';
import ApplicationPeriods from '../../components/ApplicationPeriods/ApplicationPeriods';
import { DATE } from '../../utils/functions';
import { UserAccessList } from '../../utils/auth_access';

interface ApplicationsDashboardState {
  selectedPosition: PositionAPI | null;
  selectedExams: applicationExamAPI[] | null;
  applications: GetApplicationsAPI[] | [] | null;
  selected_application: string | null;
  noRoomsApplication: NoRoomsInterface[] | null | [];
  loading: boolean;
  applicationError: string | null;
  number_of_incomplete: number,
  applicationDetails: boolean,
  selectedApplicationStatus: ApplicationStates | null,
  showPeriodFilter: boolean,
}
interface ApplicationsDashboardProps {
  auth: Auth;
  application: applicationStoreInterface;
  periods: applicationPeriodsStoreInterface;
  initiateApplication: (initial: initialApplicationInterface) => void;
  removeLocation: (course_id: string) => void;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  system: System;
  PushPopupAlert: (alert: PopupAlertData) => void;
  positions: positionsStoreInterface;
  loadAllPositions: (callback: (loading: boolean) => void) => void;
  FC_Access: Function;
}

class _ApplicationsDashboard extends Component<ApplicationsDashboardProps, ApplicationsDashboardState> {
  constructor(props: ApplicationsDashboardProps) {
		super(props);

		this.state = {
      selectedPosition: null,
      selectedExams: null,
      applications: null,
      selected_application: null,
      noRoomsApplication: null,
      loading: false,
      applicationError: null,
      number_of_incomplete: 0,
      applicationDetails: false,
      selectedApplicationStatus: null,
      showPeriodFilter: false,
		};
	}

  filteredApplications = (applications: GetApplicationsAPI[], Status: ApplicationStates | null, searchPeriod?: ApplicationPeriodGet | null) => {
    let appl:GetApplicationsAPI[];
    if (searchPeriod !== undefined && searchPeriod !== null) {
      appl = applications.filter(itm => itm.application_session.application_session_id === searchPeriod.application_session_id);
    } else {
      appl = applications;
    }
    if (Status === null) {
      return appl;
    } else {
      return appl.filter(item => item.application_status === Status);
    }
  }

  closeApplicationDetails = () => {
    this.setState({ selected_application: null });
    this.setState({ selectedPosition: null, selected_application: null })
    this.setState({ selectedExams: null });
  }

  applicationStatuses = (application: GetApplicationsAPI[]): { title: string, count: number, status: ApplicationStates }[] => {
    let output: { title: string, count: number, status: ApplicationStates }[] = [];
    for (let i in ApplicationStatesDescription) {
      output.push({
        title: ApplicationStatesDescription[i].caption,
        count: application.filter(itm => itm.application_status === ApplicationStatesDescription[i].key).length,
        status: ApplicationStatesDescription[i].key
      })
    }
    return output;
  }

  // LoadApplicationPeriods = async () => {

  // }

  LoadApplications = async () => {
    this.setState({ number_of_incomplete: 0 });
    if (this.props.auth.user.user_id) {
      const user_id = this.props.auth.user.user_id;
      const selected_position = this.state.selectedPosition;
      this.setState({ applications: null });
      this.setState({ selectedPosition: null });
      this.setState({ selectedExams: null });
      
      try {
        const res = await axios.get<GetApplicationsAPI[]>(`${API.APPLICATION}/application/${user_id}`);
        if (res.status === 200) {
          console.log("Apps: ", res.data)
          this.setState({ applications: res.data });
          if (res.data !== null) {
            if (res.data.length > 0) {
              // let done = false;
              for (let i in res.data) {
                let courses = res.data[i].position.courses;
                let exams = res.data[i].exams;
                if (res.data[i].position.position_id === selected_position?.position_id) {
                  this.setState({ selectedPosition: res.data[i].position });
                  this.setState({ selectedExams: exams });
                }
                for ( let x in courses) {
                  if (exams.filter(itm => itm.course_id === courses[x].course_id).length === 0 && res.data[i].application_status !== ApplicationStates.Rejected) {
                    this.setState({ selected_application: res.data[i].application_id });
                    this.setState({ selectedPosition: res.data[i].position, selected_application: res.data[i].application_id })
                    this.setState({ selectedExams: res.data[i].exams });
                    // done = true;
                    this.incrementIncomplete();
                  }
                  // if (done === false && this.state.selected_application === null) {
                  //   this.setState({ selected_application: res.data[0].application_id });
                  //   this.setState({ selectedPosition: res.data[0].position, selected_application: res.data[0].application_id })
                  //   this.setState({ selectedExams: res.data[0].exams });
                  // }
                }
              }
            }
          }
        }
        console.log("Applications: ", this.state.applications);
      } catch (error) {
        console.log("Applications err: ", error);
      }
    }
  }

  updateLocation = async (application_id: string, exams: sentExamInterface[]) => {
    this.setState({ loading: true });
    this.setState({ applicationError: null });
    console.log(application_id);
    for(let i in exams) {
      try {
        const res = await axios.post(`${API.APPLICATION}/application/updatemissed`, {
          application_id: application_id,
          exams: exams[i]
        });
        if (res.status === 200 && this.state.noRoomsApplication !== null) {
          this.setState({ noRoomsApplication: this.state.noRoomsApplication?.filter(itm => itm.course_id !== exams[i].course_id) });
        }
        if (res.status === 200) {
          this.props.PushPopupAlert({
            title: `Location updated successfully`,
            description: "Location has updated successfully! You can continue to update before starting of the examination!",
            theme: PopupAlertTheme.success,
          });
        }
        this.LoadApplications();
      } catch (error: any) {
        if (error.response) {
          if (error.response.data) {
            if (error.response.data.errors !== undefined) {
              if (error.response.data.errors.length > 0) {
                if (this.state.noRoomsApplication) {
                  let newArr: sentExamInterface[];
                  if (error.response.data.errors[0].field !== undefined) {
                    newArr = this.state.noRoomsApplication?.filter(itm => itm.course_id === error.response.data.errors[0].field.course_id);
                    if (newArr !== undefined)  {
                      for (let i in newArr) {
                        this.props.removeLocation(newArr[i].course_id);
                      }
                    }
                  }
                }
                this.setState({ applicationError: error.response.data.errors[0].message });
                this.props.PushPopupAlert({
                  title: error.response.data.errors[0].message,
                  theme: PopupAlertTheme.danger,
                });
              }
            } else {
              this.props.PushPopupAlert({
                title: "Something went wrong! Please try again",
                theme: PopupAlertTheme.danger,
              });
              this.setState({ applicationError: "Something went wrong! Please try again" });
            }
          }
        }
        console.log("Change error: ", {...error});
      }
    }
    if (this.state.selectedPosition) {
      this.setState({ selectedPosition: this.state.selectedPosition });
    }
    this.setState({ selected_application: application_id });
    this.setState({ loading: false });
  }

  incrementIncomplete = () => {
    this.setState({ number_of_incomplete: this.state.number_of_incomplete + 1 });
  }

  changeLoadingState = (state: boolean) => {
    this.setState({ loading: state });
  }

  deleteApplication = async (id: string) => {
    if (window.confirm("Are you sure do you want to delete this application?") && id) {
      this.changeLoadingState(true);
      try {
        await axios.delete(`${API.APPLICATION}/application/${id}`);
        this.props.PushPopupAlert({
          title: `Application deleted successfully!`,
          theme: PopupAlertTheme.success,
        });
        this.setState({ selectedExams: null });
        this.LoadApplications();
      } catch (error: any) {
        this.props.PushPopupAlert({
          title: `Failed to delete application!`,
          theme: PopupAlertTheme.warning,
        });
      }
      this.changeLoadingState(false);
    }
  }

  setShowFilterStatus = (status: boolean) => {
    this.setState({ showPeriodFilter: status})
  }

  componentDidMount = () => {
    this.props.loadApplicationPeriods(this.changeLoadingState);
    this.LoadApplications();
    this.props.loadAllPositions(this.changeLoadingState);
    if (this.props.application.apply === undefined || this.props.application.apply.currentStep === ApplicationStepsTypes.Step3) {
      this.props.initiateApplication({
        user_id: this.props.auth.user.user_id, 
        selectedPosition: null, 
        currentStep: ApplicationStepsTypes.Step1,
        positions: null,
        doTestStatus: {
          status: false,
        },
        selectedLocation: []
      });
    }
    if (this.state.applications !== null) {
      if (this.state.applications.length > 0) {
        if (this.state.selectedPosition === null) {
          this.setState({ selectedPosition: this.state.applications[0].position, selected_application: this.state.applications[0].application_id })
        }
        if (this.state.selectedExams === null) {
          this.setState({ selectedExams: this.state.applications[0].exams })
        }
      }
    }
  }
  render() {
    if (!FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).view) {
      return <Redirect to="/" />
    }
    return (
      <Fragment>
        <SideNavigation>
          {this.props.positions.positions_list === null ? <div className="btn bg-gray-500 text-white mb-4 w-full flex flex-row items-center gap-3 cursor-not-allowed animate__animated animate__zoomIn">
            <RiFileForbidLine className="text-2xl text-white" /> Please wait...
          </div> :
          this.props.positions.positions_list.length > 0 && this.props.periods.applicationPeriods && this.props.periods.applicationPeriods.filter(itm => itm.is_active === true).length !== 0 ?
          <Link to="/apply">
            <div className="btn btn-blue text-white mb-4 w-full flex flex-row items-center gap-3 animate__animated animate__zoomIn">
              <MdAdd className="text-2xl text-white" /> Apply
            </div>
          </Link> : 
          <div className="btn bg-gray-200 cursor-not-allowed text-gray-500 mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn">
            No vacancies!
          </div>}
          {/* <div className="mb-6"></div> */}
          {/* <Search data={[1, 2, 3]} /> */}
          <div 
          onClick={() => this.setState({ selectedApplicationStatus: null })} 
          className={`cursor-pointer ${this.state.selectedApplicationStatus !== null ? "text-gray-600 hover:text-blue-600 border border-gray-200 hover:border-blue-600 hover:bg-blue-100 bg-white" : "text-blue-600 border border-blue-600 hover:bg-blue-100 bg-blue-100"} flex flex-row items-center gap-2 px-2 py-1 rounded font-bold`}>
            <IoMdList className="text-xl" />
            <span>View all ({this.state.applications && this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period) ? this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period).length : 0})</span>
          </div>
          <div className="pt-1">
            {this.state.applications && this.filteredApplications(this.state.applications, null, this.props.periods.selected_period) === null ? <Loading /> : this.state.applications && this.applicationStatuses(this.filteredApplications(this.state.applications, null, this.props.periods.selected_period)).map((itm: { title: string; count: number, status: ApplicationStates }, i: number) => (
              itm.count > 0 &&
              <Fragment key={itm.title}>
                <div onClick={() => this.setState({ selectedApplicationStatus: itm.status })}>
                  <MainItem title={itm.title} count={itm.count} active={this.state.selectedApplicationStatus === itm.status ? true : false} />
                </div>
              </Fragment>
            ))}
          </div>
        </SideNavigation>
        <div className="pr-2">
          <div className="mt-2 rounded px-3 md:px-4 py-3 md:py-3">
            <div className="flex flex-col">
              <div className="flex flex-col md:flex-row justify-between md:items-center">
                <div className="flex flex-col gap-1">
                  <span className="text-blue-600 text-2xl md:text-2xl lg:text-2xl font-bold">My Applications <span className="md:hidden">({this.state.applications && this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period) ? this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period).length : 0})</span></span>
                  <div className="text-gray-700 font-bold text-xs md:text-sm lg:text-md flex flex-row gap-1 items-center"><AiOutlineFilter className="text-2xl" /> Selected period <span onClick={() => this.setState({ showPeriodFilter: true })} className={`px-2 ${this.props.periods.selected_period !== null? "text-blue-600 hover:bg-blue-100 border-2 border-gray-200 hover:border-blue-600 underline" : "bg-white text-gray-600 hover:bg-gray-100 border border-gray-300"}  rounded-full py-1 flex flex-row justify-center items-center font-bold cursor-pointer`}>{this.props.periods.selected_period !== null ? <><BsCaretRightFill className="mr-3" />{this.props.periods.selected_period.application_session_name}</> : "Choose period"}</span></div>
                </div>
                {/* <div className="hidden md:grid grid-cols-2 lg:grid-cols-2 gap-2">
                  <div className="col-span-1">
                    <div className="flex flex-col border border-gray-200 px-2 py-2 rounded">
                      <span className="text-xs md:text-xs text-blue-600 font-bold text-right">Total</span>
                      <span className="text-xl md:text-2xl font-bold text-blue-600 text-right">{this.state.applications && this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period) ? this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period).length : 0}</span>
                    </div>
                  </div>
                  <div className="col-span-1">
                    <div className="flex flex-col border border-gray-200 px-2 py-2 rounded">
                      <span className="text-xs md:text-xs text-blue-600 font-bold text-right">Incomplete</span>
                      <span className="text-xl md:text-2xl font-bold text-blue-600 text-right">{this.state.number_of_incomplete}</span>
                    </div>
                  </div>
                </div> */}
              </div>
            </div>
          </div>
          <div className="mb-2">
            <div className="grid grid-cols-12 gap-3">
              <div className="col-span-12">
                <div className={`grid md:grid-cols-1 lg:grid-cols-1 gap-2`}>
                  <div className="ml-2 -my-2 md:hidden lg:hidden xl:hidden">
                    {this.props.positions.positions_list === null ? <div className="btn bg-gray-500 text-white mb-4 w-full flex flex-row items-center gap-3 animate__animated animate__zoomIn justify-center">
                      <RiFileForbidLine className="text-2xl text-white" /> Please wait...
                    </div> :
                    this.props.positions.positions_list.length > 0 ?
                    <Link to="/apply">
                      <div className="btn btn-blue text-white mb-4 w-full flex flex-row items-center gap-3 animate__animated animate__zoomIn justify-center">
                        <MdAdd className="text-2xl text-white" /> Create application
                      </div>
                    </Link> : 
                    <div className="btn bg-gray-400 text-white mb-4 w-full flex flex-row items-center gap-3 animate__animated animate__zoomIn justify-center">
                      <RiFileForbidLine className="text-2xl text-white" /> No vacancies!
                    </div>}
                  </div>
                  {this.state.applications === null ? <LoadingV2 type={LoadingType.dark} /> :
                  this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period).length === 0 ? 
                  <div className="ml-2">
                  <NoResult 
                  theme={NoResultThemes.Dark} 
                  value="No Applications found!" 
                  component={<div className="ml-2 -my-2 hidden md:block">
                    {this.props.positions.positions_list !== null && this.props.positions.positions_list.length > 0 && this.props.periods.applicationPeriods && this.props.periods.applicationPeriods.filter(itm => itm.is_active === true).length !== 0 &&
                    <Link to="/apply">
                      <div className="text-blue-600 font-bold underline mb-2 hover:text-blue-800 animate__animated animate__zoomIn">
                        Click here to create new application
                      </div>
                    </Link>}
                  </div>} 
                  /></div> :
                  this.filteredApplications(this.state.applications, this.state.selectedApplicationStatus, this.props.periods.selected_period).map((item, i) => (
                    <div 
                      onClick={() => { 
                        this.setState({ selectedPosition: item.position, selected_application: item.application_id }); 
                        this.setState({ selectedExams: item.exams })}} 
                      className={`bg-white rounded ${this.state.selectedPosition?.position_id === item.position.position_id ? "border-2 border-blue-600 shadow" : item.application_status === ApplicationStates.Initiated ? "border-gray-200 bg-gray-50 px-3 py-3 hover:border-gray-500" : item.application_status === ApplicationStates.Rejected ? "px-3 py-3 bg-white border-white" : item.application_status === ApplicationStates.Shortlisted ? "border-gray-200 bg-white px-3 py-3 hover:border-blue-600" : "px-3 py-3 bg-white border-yellow-600 hover:border-yellow-500"} flex flex-col cursor-pointer font-normal hover:font-bold border ml-2`}>
                      <div className={`grid md:grid-cols-12 ${this.state.selectedPosition?.position_id === item.position.position_id ? "bg-gray-100 pr-3 rounded-t py-2" : "bg-white"}`}>
                        <div className="col-span-1 flex items-center justify-center">
                          {item.application_status === ApplicationStates.Initiated ?
                          <IoBriefcaseOutline className="text-5xl text-gray-600" /> :
                          item.application_status === ApplicationStates.Rejected ?
                          <IoBriefcase className="text-5xl text-yellow-900" /> :
                          item.application_status === ApplicationStates.Shortlisted ?
                          <IoBriefcaseOutline className="text-5xl text-blue-600" /> :
                          <IoBriefcase className="text-5xl text-yellow-700" />}
                        </div>
                        <div className="col-span-7">
                          <span className={`font-extrabold ${item.application_status !== ApplicationStates.Initiated ? item.application_status === ApplicationStates.Rejected ? "text-yellow-900" : item.application_status === ApplicationStates.Shortlisted ? "text-blue-600" : "text-yellow-700" : "text-blue-600"}`}>{item.position.position_title}</span>
                          <div className="flex flex-col text-xs text-gray-600 mt-3">
                            <div className="flex flex-row gap-2">
                              <span className={`${item.application_status !== ApplicationStates.Initiated ? item.application_status === ApplicationStates.Rejected ? "text-yellow-900" : item.application_status === ApplicationStates.Shortlisted ? "text-blue-600" : "text-yellow-700" : "text-gray-500"}`}>Date:</span>
                              <span className={`${item.application_status !== ApplicationStates.Initiated ? item.application_status === ApplicationStates.Rejected ? "text-yellow-900" : item.application_status === ApplicationStates.Shortlisted ? "text-blue-600" : "text-yellow-700" : "text-black"} font-bold`}>{DATE(item.createdAt, "DD/MM/YYYY")}</span>
                            </div>
                            <div className="flex flex-row gap-2">
                              <span className={`${item.application_status !== ApplicationStates.Initiated ? item.application_status === ApplicationStates.Rejected ? "text-yellow-900" : item.application_status === ApplicationStates.Shortlisted ? "text-blue-600" : "text-yellow-700" : "text-gray-500"}`}>Application period:</span>
                              <span className={`${item.application_status !== ApplicationStates.Initiated ? item.application_status === ApplicationStates.Rejected ? "text-yellow-900" : item.application_status === ApplicationStates.Shortlisted ? "text-blue-600" : "text-yellow-700" : "text-black"} font-bold`}>{item.application_session && item.application_session.application_session_name} - {item.application_session && item.application_session.period}</span>
                            </div>
                          </div>
                        </div>
                        <div className="col-span-4 flex md:justify-end items-center">
                          <div className="flex flex-col float-left text-sm md:justify-end">
                            <span className={`${item.application_status === ApplicationStates.Rejected ? "text-yellow-700" : "text-gray-500"} md:text-right`}>Status</span>
                            {item.application_status === ApplicationStates.Initiated ? 
                            <span className={`font-bold ${this.state.selectedPosition?.position_id === item.position.position_id ? "bg-gray-600 text-gray-100 border border-gray-500 rounded-full" : "bg-gray-50 text-gray-800 rounded"} px-3 py-1`}>{ApplicationStatesDescription.find(itm => itm.key === item.application_status)?.caption }</span>:
                            item.application_status === ApplicationStates.Rejected ? 
                            <span className={`font-bold ${this.state.selectedPosition?.position_id === item.position.position_id ? "bg-yellow-900 text-yellow-100 border border-yellow-700 rounded-full" : "bg-yellow-100 text-yellow-900 rounded"} px-3 py-1`}>{ApplicationStatesDescription.find(itm => itm.key === item.application_status)?.caption }</span> :
                            item.application_status === ApplicationStates.Shortlisted ? 
                            <span className={`font-bold ${this.state.selectedPosition?.position_id === item.position.position_id ? "bg-blue-900 text-blue-100 border border-blue-700 rounded-full" : "bg-blue-100 text-blue-900 rounded"} px-3 py-1`}>{ApplicationStatesDescription.find(itm => itm.key === item.application_status)?.caption }</span> :
                            <span className={`font-bold ${this.state.selectedPosition?.position_id === item.position.position_id ? "bg-yellow-600 text-yellow-100 border border-yellow-500 rounded-full" : "bg-yellow-100 text-yellow-700 rounded"} px-3 py-1`}>{ApplicationStatesDescription.find(itm => itm.key === item.application_status)?.caption }</span>
                            }
                          </div>
                        </div>
                      </div>
                      
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
        {this.state.selectedPosition !== null && this.state.selectedExams !== null &&
        <Modal
          title={<div className="flex flex-row gap-4 items-center">
            <IoBriefcaseOutline className="text-5xl text-blue-600" />
            <div className="flex flex-col">
              <span className="font-bold text-xl text-blue-600">{this.state.selectedPosition.position_title}</span>
              <span className="text-sm text-gray-500 font-normal pl-1">
                {this.state.applications !== null && this.state.selectedPosition !== null && this.state.applications.find(itm => itm.position.position_id === this.state.selectedPosition?.position_id)?.application_session.application_session_name} - {this.state.applications !== null && this.state.selectedPosition !== null && this.state.applications.find(itm => itm.position.position_id === this.state.selectedPosition?.position_id)?.application_session.period}
              </span>
              </div>
            </div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.closeApplicationDetails()}
          backDropClose={true}
          widthSizeClass={ModalSize.maxWidth}
          displayClose={true}
          padding={{title: true,}} >
            <div className="border-t border-gray-200 -mt-2 px-2 pb-2">
                <Exams 
                  selectedExams={this.state.selectedExams}
                  selectedPosition={this.state.selectedPosition}
                  selected_application={this.state.selected_application}
                  updateLocation={this.updateLocation}
                  changeLocationError={this.state.applicationError}
                  loading={this.state.loading}
                  incrementIncomplete={this.incrementIncomplete}
                  applicationHistory={this.state.applications !== null && this.state.selectedPosition !== null ? this.state.applications.find(itm => itm.position.position_id === this.state.selectedPosition?.position_id)?.application_history : undefined}
                  deleteApplication={this.deleteApplication}
                  selectedApplicationDetails={this.state.applications && this.state.applications.find(itm => itm.application_id === this.state.selected_application)!}
                />
            </div>
        </Modal>}
        {this.state.showPeriodFilter === true &&
        <Modal
          title={<div className="text-2xl">Filter by period</div>}
          backDrop={true}
          theme={Themes.default}
          close={() => this.setState({ showPeriodFilter: false })}
          backDropClose={true}
          widthSizeClass={ModalSize.extraLarge}
          displayClose={true}
          padding={{title: true, body: true}} >
            <ApplicationPeriods 
              applicationPeriods={this.props.periods.applicationPeriods} 
              setSelectedPeriod={this.props.setSelectedPeriod} 
              showPeriodFilter={this.setShowFilterStatus}
            />
        </Modal>}
      </Fragment>
    )
  }
}

const mapStateToProps = ({
  auth,
  application,
  periods,
  system,
  positions,
}: StoreState): { auth: Auth; application: applicationStoreInterface, periods: applicationPeriodsStoreInterface; system: System; positions: positionsStoreInterface, } => {
  return { auth: auth, application: application, periods, system: system, positions: positions, };
};

export const ApplicationsDashboard = connect(mapStateToProps, {
  initiateApplication,
  removeLocation,
  loadApplicationPeriods,
  setSelectedPeriod,
  PushPopupAlert,
  loadAllPositions,
  FC_Access,
})(_ApplicationsDashboard);