import React from 'react'
import { applicationHistory, ApplicationStatesDescription } from '../../../interfaces/application'
import { DATE } from '../../../utils/functions'

interface ApplicationHistoryProps {
  applicationHistory: applicationHistory[];
}

const ApplicationHistory = (props: ApplicationHistoryProps) => {
  return (
    <div className="flex flex-col justify-between items-start gap-1 bg-blue-50 px-3 py-2">
      {/* <span className="text-sm text-blue-800 font-bold">Application status history</span> */}
      <hr className="bg-blue-600" />
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-3">
      {props.applicationHistory !== undefined &&
        props.applicationHistory.length > 0 && 
        props.applicationHistory.map((item, i) => (
          <div key={i + 1} className="col-span-1 flex flex-col border border-blue-500 rounded px-3 py-2">
            <div className="flex flex-row gap-2 justify-start items-center text-xs">
              <span className="text-gray-500">Date:</span>
              <span className="font-bold">{DATE(item.date, "DD/MM/YYYY")}</span>
            </div>
            <div className="flex flex-row gap-2 justify-start items-center text-xs">
              <span className="text-gray-500">Action:</span>
              <span className="font-bold">{item.comment}</span>
            </div>
            <div className="flex flex-row gap-2 justify-start items-center text-xs">
              <span className="text-gray-500">Application status:</span>
              <span className="font-bold">{ApplicationStatesDescription.find(itm => itm.key === item.application_status)?.caption}</span>
            </div>
          </div>
        ))}
      </div> 
    </div>
  )
}

export default ApplicationHistory
